<template>
  <el-dialog v-model="editDialogVisible" :title="title" :before-close="handleClose" width="600px" draggable>
    <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        inline
        label-width="80px"
        label-position="right"
        status-icon>
      <el-form-item label="类型" prop="type" style="width: 100%;">
        <el-radio-group v-model="editForm.type">
          <el-radio label="F">目录</el-radio>
          <el-radio label="M">菜单</el-radio>
          <el-radio label="P">权限</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="父节点" prop="parentId" style="width: 48%;">
        <el-select v-model="editForm.parentId" placeholder="请选择上级菜单">
          <el-option label="顶级菜单" :value="0"/>
          <el-option v-for="menu in menuListTree" :key="menu.id" :label="menu.name" :value="menu.id"/>
        </el-select>
      </el-form-item>
      <el-form-item label="状态" prop="disabled" style="width: 40%;">
        <el-switch
            v-model="editForm.disabled"
            style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
            active-text="启用"
            inactive-text="禁用"
            :active-value="false"
            :inactive-value="true"
            width="50px"
        />
      </el-form-item>
      <el-form-item label="名称" prop="name" style="width: 48%;">
        <el-input v-model="editForm.name"/>
      </el-form-item>
      <el-form-item label="图标" prop="icon" style="width: 40%;">
        <el-select
            v-model="editForm.icon"
            filterable
            allow-create
            default-first-option
            :reserve-keyword="false"
            :suffix-icon="editForm.icon"
            :disabled="iconDisabled"
        >
          <el-option
              v-for="item in icons"
              :key="item"
              :label="item"
              :value="item"
          />
        </el-select>
        <!--        <el-icon :size="16"><component v-if="editForm.icon!=''" :is="editForm.icon"/></el-icon>-->
      </el-form-item>
      <el-form-item label="组件路径" prop="component" style="width: 48%;">
        <el-input v-model="editForm.component" :disabled="componentDisabled"/>
      </el-form-item>
      <el-form-item label="路由路径" prop="path" style="width: 40%;">
        <el-input v-model="editForm.path" :disabled="pathDisabled"/>
      </el-form-item>
      <el-form-item label="权限代码" prop="perms" style="width: 48%;">
        <el-input v-model="editForm.perms" :disabled="permsDisabled"/>
      </el-form-item>
      <el-form-item label="相对顺序" prop="sequence" style="width: 40%;">
        <el-input-number v-model="editForm.sequence" :min="1" :max="10"/>
      </el-form-item>
      <el-form-item label="备注" prop="note" style="width: 100%;">
        <el-input v-model="editForm.note" type="textarea" :rows="5"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="handleReset(id)">重置</el-button>
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleConfirm">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup>
import {defineProps, defineEmits, computed, ref, watch, reactive} from 'vue';
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessageBox, ElMessage} from "element-plus";

// 定义传入该子组件的参数
const props = defineProps({
  editDialogVisible: {
    type: Boolean,
    required: true,
    default: false
  },
  id: {
    type: Number,
    required: true,
    default: -1
  },
  title: {
    type: String,
    required: true,
    default: ''
  }
});

// 定义组件（自定义）事件
const emit = defineEmits(['update:editDialogVisible', 'handleSubmit']);

// 定义“编辑”对话框的显示状态
const editDialogVisible = computed({
  // 只读！读取时从父组件获取，更新时更新父组件
  get() {
    return props.editDialogVisible;
  },
  set(newValue) {
    emit('update:editDialogVisible', newValue);
  }
});

// 定义表单数据
const editForm = ref({
