  parentId: 0,
  type: '',
  name: '',
  sequence: 1,
  icon: '',
  perms: '',
  component: '',
  path: '',
  disabled: false,
  note: '',
});

// 定义上级菜单树
const menuListTree = ref([]);

// 定义图标数据源
