// 定义部分元素是否禁用
const iconDisabled = ref(false);
const permsDisabled = ref(false);
const componentDisabled = ref(false);
const pathDisabled = ref(false);


const validateMenu = async (rule, value, callback) => {
  // console.log(rule.field)
  // console.log(value)

  let url;
  // 基础格式验证通过后，才给 url 赋值。
  if (rule.field == 'name') {
    if (value && value.length > 16) {
      callback(new Error('菜单名称长度不可超过16位'));
    } else {
      url = '/api/sys/menus/checkMenu?id=' + editForm.value.id + '&name=' + value;
    }
  } else if (rule.field == 'perms') {
    if (value && value.length > 32) {
      callback(new Error('权限代码长度不可超过32位'));
    } else {
      url = '/api/sys/menus/checkMenu?id=' + editForm.value.id + '&perms=' + value;
    }
  } else if (rule.field == 'parentId') {
    if (value == -1) {
      callback(new Error('上级菜单不能为空'));
    } else {
      callback();
    }
  } else if (rule.field == 'icon') {
    if (value && value.length > 32) {
      callback(new Error('图标长度不可超过32位'));
    } else {
      callback();
    }
  } else if (rule.field == 'component') {
    if (value && value.length > 128) {
      callback(new Error('组件路径长度不可超过128位'));
    } else {
      callback();
    }
  } else if (rule.field == 'path') {
    if (value && value.length > 128) {
      callback(new Error('路由路径长度不可超过128位'));
    } else {
      callback();
    }
  }
  // url 有值，才向后台发送验证请求。
  // console.log(url);
  if (url) {
    // console.log('向后台发送请求');
    let res = await AxiosUtil.post(url);
    // console.log(res.data);
    if (res.data.code == 1) {
      callback(new Error(res.data.msg));
    } else {
      callback();
    }
  }
}

// 定义表单验证规则
const editFormRules = reactive({
  name: [
    {required: true, message: '菜单名称不能为空', trigger: 'blur'},
    {validator: validateMenu, trigger: 'change'},
    {validator: validateMenu, trigger: 'blur'},
  ],
  parentId: [
    {required: true, message: '上级菜单不能为空', trigger: 'blur'},
  ],
  type: [
    {required: true, message: '类型不能为空', trigger: 'blur'},
  ],
  sequence: [
    {required: true, message: '序号不能为空', trigger: 'blur'},
  ],
  icon: [
    {validator: validateMenu, trigger: 'blur'},
  ],
  perms: [
    {validator: validateMenu, trigger: 'change'},
    {validator: validateMenu, trigger: 'blur'},
  ],
  component: [
    {required: false, max: 128, message: '组件路径长度不可超过128个字符', trigger: 'blur'},
  ],
  path: [
    {required: false, max: 128, message: '路由路径长度不可超过128个字符', trigger: 'blur'},
  ],
  disabled: [
    {required: true, message: '状态不能为空', trigger: 'blur'},
  ],
  note: [
    {required: false, max: 192, message: '备注信息不可超过192个字符', trigger: 'blur'},
  ],
})

// 定义表单引用
const editFormRef = ref(null);

// 表单重置事件
const handleReset = async (id) => {
  if (id == -1) {
    editForm.value = {
      id: -1,
      parentId: 0,
      type: '',
      name: '',
      sequence: 1,
      icon: '',
      perms: '',
      component: '',
      path: '',
      disabled: false,
      note: '',
    };
    console.log('清空表单...');
  } else {
    let res = await AxiosUtil.get('/api/sys/menus/' + id);
    console.log(res.data);
    if (res.data.code == 20010) {
      let menu = res.data.menu;
      // 更新表格数据
      editForm.value = menu;
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
    }
    console.log('从后台填充...');
  }
  // 无论是新增还是编辑，都需要向后台请求父节点列表
  let res = await AxiosUtil.get('/api/sys/menus');
  console.log(res.data);
  if (res.data.code == 20010) {
    let menus = [];
    res.data.menuListTree.forEach(item => {
      if (item.type == "F" || item.type == "M") {
        item.name = "+ " + item.name;
        menus.push(item);
        item.children.forEach(child => {
          if (child.type == "M") {
            child.name = "••• " + child.name;
            menus.push(child);
          }
        })
      }
    });
    // 更新上级菜单列表
    menuListTree.value = menus;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 定义关闭对话框前的确认操作
const handleClose = (done) => {
  ElMessageBox.confirm('确认在未保存的情况下关闭对话框？')
      .then(() => {
        done()
      })
      .catch(() => {
        // catch error
      })
}

// 定义对话框关闭事件
const handleCancel = () => {
  editDialogVisible.value = false;
}

// 定义确认按钮事件
const handleConfirm = () => {
  // 表单验证
  editFormRef.value.validate(async valid => {
    if (valid) {
      // 表单验证通过
      let url, res;
      if (editForm.value.id == -1) {
        console.log('提交新增...');
        url = '/api/sys/menus';
        res = await AxiosUtil.post(url, editForm.value);
        console.log("新增菜单 res.data= ", res.data);
      } else {
        console.log('提交修改...');
        url = '/api/sys/menus';
        res = await AxiosUtil.put(url, editForm.value);
        console.log("修改菜单 res.data= ", res.data);
      }
      if (res.data.code == 20020 || res.data.code == 20030) {
        ElMessage({
          message: res.data.msg,
          type: 'success',
        });
        emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
        handleCancel(); // 关闭对话框
      } else {
        ElMessage({
          message: res.data.msg,
          type: 'error',
        });
      }
    }
  });
}

// 当对话框重新显示时重置表单
watch(editDialogVisible, (newValue) => {
  if (newValue == true) {
    handleReset(props.id);
  }
})

// 当菜单类型变化时，禁用部分元素
watch(editForm, (newValue) => {
  if (newValue.type == 'F') {
    iconDisabled.value = false;
    permsDisabled.value = true;
    componentDisabled.value = true;
    pathDisabled.value = false;
  } else if (newValue.type == 'M') {
    iconDisabled.value = false;
    permsDisabled.value = true;
    componentDisabled.value = false;
    pathDisabled.value = false;
  } else if (newValue.type == 'P') {
    iconDisabled.value = true;
    permsDisabled.value = false;
    componentDisabled.value = true;
    pathDisabled.value = true;
  }
}, {deep: true})

</script>


<script>
export default {
  name: "EditDialog"
}
</script>

