<template>
  <el-container>
    <!--操作台-->
    <el-header class="operatingFloor">
      <el-row :gutter="20">
        <el-button type="success" :icon="DocumentAdd" @click="handleEdit(null)" style="margin-left: 12px;" v-if="hasAnyAuthority('system:menu:add')">新增</el-button>
      </el-row>
    </el-header>
    <el-main style="padding: 5px; background: aliceblue;" id="mainView">
      <el-table
          :data="tableData"
          :max-height="tableMaxHeight"
          row-key="id"
          :indent="25"
          size="small"
          style="width: 100%"
          highlight-current-row
          default-expand-all
          stripe>
        <el-table-column prop="name" label="权限名称" width="160" header-align="center" fixed/>
        <el-table-column prop="icon" label="图标" width="55" align="center">
          <template #default="{row}">
            <el-icon :size="20">
              <component v-if="row.icon!=''" :is="row.icon"/>
            </el-icon>
          </template>
        </el-table-column>
        <el-table-column prop="sequence" label="序号" width="55" align="center"/>
        <el-table-column prop="perms" label="权限代码" width="180" header-align="center"/>
        <el-table-column prop="component" label="组件路径" width="150" header-align="center"/>
        <el-table-column prop="path" label="路由路径" width="120" header-align="center"/>
        <el-table-column prop="disabled" label="状态" width="76" align="center">
          <template #default="{row}">
            <el-switch
                v-model="row.disabled"
                inline-prompt
                style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
                active-text="启用"
                inactive-text="禁用"
                :active-value="false"
                :inactive-value="true"
                width="50px"
                :loading="row.loading"
                @change="handleStateChange(row)"
                :disabled="!hasAnyAuthority('system:menu:edit')"
            />
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="创建时间" width="160" header-align="center"/>
        <el-table-column prop="updateTime" label="更新时间" width="160" header-align="center"/>
        <el-table-column prop="note" label="备注" min-width="150" header-align="center"/>
        <el-table-column label="操作" width="130" align="right" header-align="center" fixed="right">
          <template #default="scope">
            <el-button type="primary" :icon="Edit" @click="handleEdit(scope.row.id)" v-if="hasAnyAuthority('system:menu:edit')"/>
            <el-popconfirm title="您确定要删除这条记录吗？" @confirm="handleDelete(scope.row.id)" v-if="hasAnyAuthority('system:menu:delete')">
              <template #reference>
                <el-button type="danger" :icon="Delete"/>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
  <EditDialog v-model:edit-dialog-visible="editDialogVisible" :id="editId" :title="editTitle"
              @handleSubmit="fetchData"/>
</template>

<script setup>
import {DocumentAdd, Delete, Edit,} from '@element-plus/icons-vue'
import {onMounted, ref} from "vue";
import AxiosUtil from "@/utils/AxiosUtil";
import EditDialog from "./EditDialog";
import {ElMessage} from "element-plus";

/*====以下为一些数据的定义====*/

// 定义表格的最大高度
const tableMaxHeight = ref(400);

// 定义表格数据
const tableData = ref([]);

// 定义编辑对话框的显示状态
const editDialogVisible = ref(false);

// 定义传入编辑对话框的id值
const editId = ref(-1);

// 定义传入编辑对话框的title值
const editTitle = ref('');


/*====以下为各按钮的点击事件====*/

// 处理删除记录
const handleDelete = async (id) => {
  console.log('删除...', id);
  let url = '/api/sys/menus/' + id;
  console.log('url=', url);
  let res = await AxiosUtil.del(url);
  console.log('res=', res);
  if (res.data.code == 20040) {
    ElMessage({
      message: res.data.msg,
      type: 'success',
    });
    await fetchData();
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 处理新增、编辑
const handleEdit = (id) => {
  if (id) {
    console.log('修改...', id);
    editId.value = id;
    editTitle.value = '权限修改';
  } else {
    console.log('新增...', id);
    editId.value = -1;
    editTitle.value = '权限添加';
  }
  editDialogVisible.value = true;
}


/*====以下为一些监听以及其他函数====*/

// 处理状态的改变
const handleStateChange = async (row) => {
  // console.log('状态变化...', row.id);

  // 先设置元素为加载状态
  row.loading = true;
  let url = '/api/sys/menus/updateDisabled/' + row.id + '/' + row.disabled;
  // console.log(url);

  // 为保证程序能正常走完，发送请求要用 try...catch 包围
  try {
    let res = await AxiosUtil.put(url);
    if (res.data.code == 20030) {
      ElMessage({
        message: res.data.msg,
        type: 'success',
      });
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
      row.disabled = !row.disabled; // 恢复显示为修改前的状态
    }
  } catch (e) {
    console.log('e=', e);
    ElMessage({
      message: '操作失败！' + e,
      type: 'error',
    });
    row.disabled = !row.disabled; // 恢复显示为修改前的状态
  }

  // 请求结束后取消元素的加载状态
  row.loading = false;
}

// 从后台获取数据
const fetchData = async () => {
  let url = '/api/sys/menus';
  console.log('url=', url);
  // 从后台获取数据
  let res = await AxiosUtil.get(url);
  console.log("更新菜单列表 res= ", res);
  if (res.data.code == 20010) {
    // 更新表格数据
    let menuList = res.data.menuListTree;
    tableData.value = menuList;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

fetchData();

// 新建一个ResizeObserver用于监视表格容器尺寸的变化
const resizeObserver = new ResizeObserver((entries) => {
  for (const entry of entries) {
    // 仅对指定id的元素生效
    if (entry.target.id == 'mainView') {
      let mainViewHeight;
      // 由于兼容性问题，获取元素高度时需谨慎
      // https://developer.mozilla.org/zh-CN/docs/Web/API/ResizeObserver
      // https://developer.mozilla.org/zh-CN/docs/Web/API/ResizeObserverEntry
      if (entry.contentBoxSize) {
        // Firefox implements `contentBoxSize` as a single content rect, rather than an array
        const contentBoxSize = Array.isArray(entry.contentBoxSize) ? entry.contentBoxSize[0] : entry.contentBoxSize;
        mainViewHeight = contentBoxSize.blockSize;
      } else {
        mainViewHeight = entry.contentRect.height;
      }
      // 设置表格的最大高度为表格容器的高度
      tableMaxHeight.value = mainViewHeight;
      // console.log('mainViewHeight= ', mainViewHeight);
    }
  }
});

// 在组件完成初始渲染并创建 DOM 节点后开始监视表格容器
onMounted(() => {
  const mainView = document.getElementById('mainView');
  resizeObserver.observe(mainView);
});


</script>

<script>
export default {
  name: "index"
}
</script>

<style scoped>
/*操作台*/
.operatingFloor {
  /*display: flex;*/
  /*align-items: center; !*align-items 属性可以使元素在交叉轴方向对齐*!*/
  /*justify-content: flex-start; !*justify-content属性用来使元素在主轴方向上对齐*!*/
  height: 48px;
  padding: 8px 10px;

}

.el-tag--small {
  margin-left: 5px;
}

</style>